@import "src-doc/doc.global.scss";





























.app-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: solid 1px #dcdee2;
    box-shadow: 0 0 2px 1px #f1f1f1;
    background-color: white;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
}
