@import "src-doc/doc.global.scss";






























.app-content {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    z-index: 0;

    & > .pl-scroll .pl-vertical-scrollbar-wrapper .pl-vertical-scrollbar {
        z-index: 3;
    }

    .app-content-body {
        padding: 16px 0;
    }

