@import "src-doc/doc.global.scss";


























































.demo-row {
    .demo-row-title {
        border-bottom: dashed 1px #f1f1f1;
        font-size: 14px;
        padding: 14px 24px;
        color: #606266;

        span {
            cursor: pointer;
        }
    }

    .demo-row-content {
        padding: 16px 24px;
        font-size: 14px;

        & > *:not(.demo-line) {
            margin-right: 16px;
        }
    }

    .demo-row-icon-expand {
        transform: rotate(90deg);
        transition: all linear 300ms;
        margin-left: 12px;
    }

    &.demo-row-show {
        .demo-row-icon-expand {
            transform: rotate(-90deg);
        }
    }
}
